@import url(http://fonts.googleapis.com/css?family=Raleway:200,400,700,800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: Raleway;
}

.App{
  position: absolute;
  right: 0px;
  left: 0px;
}

.sections{
  position: absolute;
  top: 0px;
  right: 0px;
  left: 0px;
  z-index: 0;
}
nav{
    width: calc(100% - 30px);
    padding: 15px;
    overflow: hidden;
    position: fixed;
    z-index: 100;
}

nav img{
    float: left;
    margin-top: 3px;
    margin-left: 10px;
}

nav ul {
    list-style: none;
    padding: 0px;
    margin: 0px;
    float: right;
    margin-right: 5px;
}

nav ul li {
    display: inline-block;
    margin: 5px 15px;
    font-size: 14px;
    color: white;
    font-weight: 700;
}

.scrolled{
    background-color: #2d2d2d !important;
    transition: background-color 100ms linear;
}
.hero{
    background: green;
    height: 100vh;
    background-image: linear-gradient(#3B86DC, #182B42);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

h1{
    color: white;
    font-size: calc(20px + 2vw);
    font-weight: 400;
}

h1 span{
    font-weight: 700;
}
.projects{
}

.projects .container{
    margin: auto;
    width: 850px;
    max-width: 80%;
    padding: 100px 0px;
}

.projects h1{
    font-weight: 700;
    font-size: 36px;
    color: #182e43;
    display: inline-block;
    margin: 0px;
}

.projects-grid{
    margin-top: 50px;
    width: calc(100% + 20px);
    text-align: center;
    margin-left: -10px;
}

@media only screen and (max-width: 850px) {
    .projects h1{
        font-size: 28px;
        margin-top: 5px;
    }
 }
 
.thumbnail{
    width: calc(33.333% - 20px);
    height: 170px;
    margin-bottom: 30px;
    border-radius: 10px;
    background: green;
    display: inline-block;
    margin: 10px;
    text-align: left;
    position: relative;
}

.thumbnail b{
    color: white;
    position: absolute;
    bottom: 15px;
    left: 15px;
}

@media only screen and (max-width: 850px) {
   .thumbnail{
        width: calc(50% - 20px);
   }
}

@media only screen and (max-width: 600px) {
    .thumbnail{
         width: calc(100% - 20px);
         margin: 10px;
    }
 }
